.mastering-section {
  font-family: Helvetica, Arial, sans-serif; /* Set font to Helvetica with fallbacks */
}

.mastering-section {
  background-color: rgba(0, 0, 0, 0); /* Adjust the alpha value as needed */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  padding: 20px; /* Adjust padding as needed */
}

h2 {
  font-size: 2rem; /* Adjust font size as needed */
  margin-bottom: 10px; /* Adjust margin as needed */
}

p {
  font-size: 1.2rem; /* Adjust font size as needed */
  margin-bottom: 20px; /* Adjust margin as needed */
  color: #efe7e4; /* Set text color to #efe7e4 */
}

button {
  padding: 10px 20px; /* Adjust padding as needed */
  font-size: 1.2rem; /* Adjust font size as needed */
  background-color: rgba(58, 58, 58, 0.777); /* Change background color to black */
  color: #fff; /* Adjust text color as needed */
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease, box-shadow 0.3s ease; /* Add transition effect as needed */
  box-shadow: 0 4px 6px rgb(222, 222, 222); /* Add box shadow for 3D effect */
  border-radius: 5px; /* Add border radius for rounded corners */
}

button:hover {
  background-color: #3367d6; /* Adjust hover background color as needed */
  animation: rainbow 5s infinite; /* Apply the rainbow animation on hover */
  box-shadow: none; /* Remove box shadow on hover */
}

@keyframes rainbow {
  0% { background-color: red; opacity: 0; }
  20% { background-color: orange; opacity: 1; }
  40% { background-color: green; opacity: 1; }
  60% { background-color: blue; opacity: 1; }
  80% { background-color: indigo; opacity: 1; }
  100% { background-color: violet; opacity: 1; }
}
