/* PasswordPage.css */

.password-page {
  background-color: #efe7e4;
  display: flex;
  flex-direction: column;
  justify-content: space-between; /* Change from center to space-between */
  align-items: center;
  height: 100vh;
  transition: opacity 1s ease-in-out; /* Fade-out animation duration */
}

.password-page.hidden {
  opacity: 0;
}

.images-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.password-image {
  width: 100px;
  height: 100px;
  cursor: pointer;
  transition: transform 0.2s ease-in-out, filter 0.2s ease-in-out; /* Adjust the transition duration */
}

.password-image:hover {
  transform: scale(1.1) rotate(5deg);
  filter: brightness(1.2);
}

.password-image.clicked {
  transform: scale(1.2) rotate(0);
}

.images-container.animate img {
  animation: fadeInUp 0.8s ease-in-out; /* Adjust the animation duration as needed */
}

.images-container.exit img {
  animation: scatter 0.5s ease-in-out; /* Adjust the exit animation duration as needed */
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes scatter {
  0% {
    transform: translateX(0);
  }
  25%,
  75% {
    transform: translateX(-20px);
  }
  50% {
    transform: translateX(20px);
  }
  100% {
    transform: translateX(20px);
    opacity: 0;
  }
}
